body.landing:before {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-variant: normal;
  text-rendering: auto;
  transform-origin: 50%;
  color: #ffffff26;
  content: "";
  cursor: default;
  opacity: 0;
  text-align: center;
  z-index: -1;
  width: 2em;
  height: 2em;
  margin: -1em 0 0 -1em;
  font-family: "Font Awesome 5 Free";
  font-size: 2em;
  font-style: normal;
  font-weight: 900;
  line-height: 2em;
  display: block;
  position: fixed;
  top: 50%;
  left: 50%;
  text-transform: none !important;
  -ms-animation: spinner-show 1.5s 1 .25s ease forwards, spinner-hide .25s ease-in-out forwards !important;
  animation: 1.5s .25s forwards spinner-show, .25s ease-in-out forwards spinner-hide !important;
}

body.landing:after {
  content: "";
  opacity: 0;
  z-index: -1;
  background: #1c1d26;
  width: 100%;
  height: 100%;
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  -ms-animation: overlay-hide 1.5s ease-in forwards !important;
  animation: 1.5s ease-in forwards overlay-hide !important;
}

body.landing.is-preload:before {
  z-index: 100001;
  -ms-animation: spinner-show 1.5s 1 .25s ease forwards, spinner-rotate .75s infinite linear !important;
  animation: 1.5s .25s forwards spinner-show, .75s linear infinite spinner-rotate !important;
}

body.landing.is-preload:after {
  opacity: 1;
  z-index: 100000;
  -ms-animation: none !important;
  animation: none !important;
}

@media (-webkit-device-pixel-ratio >= 2) {
  body.landing:before {
    line-height: 2.025em;
  }
